import { Form, Input, Button, Checkbox, Divider } from "antd"
import { useHistory } from "react-router-dom"
import { MobileOutlined, LockOutlined } from "@ant-design/icons"
import axios from "axios"

import "./login.less"
import { useRef } from "react"

const Login = () => {
	// 设置手机号码和密码的useRef hooks
	const userPhoneRef = useRef()
	const userPasswordRef = useRef()
	// 设置history的hooks
	const history = useHistory()
	// 提交表单数据，向服务器发送验证请求
	async function onFinish() {
		const userPhone = userPhoneRef.current.state.value
		const userPassword = userPasswordRef.current.state.value
		return await axios.get("../TestData/testdata.json").then((res) => {
			userPhone === res.data.userphone &&
			userPassword === res.data.password
				? setTimeout(() => {
						history.push("/home")
				  }, 1500)
				: console.log("手机号码或密码不正确")
		})
	}

	return (
		<>
			<div className="containerLogin">
				<Form
					name="normal_login"
					className="testing"
					initialValues={{
						remember: true,
					}}
					onFinish={onFinish}
					// style={{ width: "300px" }}
				>
					<h1 style={{ textAlign: "center" }}>欢迎来到Chatter!</h1>
					<Divider />
					<Form.Item
						name="phone"
						rules={[
							{
								required: true,
								message: "请输入您的手机号码!",
							},
						]}
					>
						<Input
							prefix={
								<MobileOutlined className="site-form-item-icon" />
							}
							placeholder="手机号码"
							ref={userPhoneRef}
						/>
					</Form.Item>
					<Form.Item
						name="password"
						rules={[
							{
								required: true,
								message: "请输入密码",
							},
						]}
					>
						<Input
							prefix={
								<LockOutlined className="site-form-item-icon" />
							}
							type="password"
							placeholder="密码"
							ref={userPasswordRef}
						/>
					</Form.Item>
					<Form.Item>
						<Form.Item
							name="remember"
							valuePropName="checked"
							noStyle
						>
							<Checkbox>记住我</Checkbox>
						</Form.Item>

						{/* TODO 忘记密码跳转页面 */}
						<a className="login-form-forgot" href="/notfound">
							忘记密码
						</a>
					</Form.Item>

					<Form.Item>
						<Button
							type="primary"
							htmlType="submit"
							className="loginFormButton"
						>
							登录
						</Button>
						{/* TODO 注册页面跳转 */}
						或者 <a href="/notfound">立即注册</a>
					</Form.Item>
				</Form>
			</div>
		</>
	)
}

export default Login
