const Channels = (state = initialState, action) => {
	switch (action.type) {
		case ADD_CHANNEL:
			return [
				...state,
				{
					id: `${dateNow.toDateString()}}`,
					tagId: `'#'+${dateNow.toDateString}`,
					name: "newGame",
					avatar: "icon-battle-net",
					tags: [],
				},
			]
		case DELETE_CHANNEL:
			return state.filter((channelObj) => {
				return channelObj.id !== action.id
			})

		default:
			return state
	}
}
export default Channels
