import React, { createElement, useState } from "react"
import { Comment, Tooltip, Avatar } from "antd"
import moment from "moment"
import {
	DislikeOutlined,
	LikeOutlined,
	DislikeFilled,
	LikeFilled,
} from "@ant-design/icons"

const RoomSite = () => {
	const [likes, setLikes] = useState(0)
	const [dislikes, setDislikes] = useState(0)
	const [action, setAction] = useState(null)

	const like = () => {
		setLikes(1)
		setDislikes(0)
		setAction("liked")
	}

	const dislike = () => {
		setLikes(0)
		setDislikes(1)
		setAction("disliked")
	}

	const actions = [
		<Tooltip key="comment-basic-like" title="Like">
			<span onClick={like}>
				{createElement(action === "liked" ? LikeFilled : LikeOutlined)}
				<span className="comment-action">{likes}</span>
			</span>
		</Tooltip>,
		<Tooltip key="comment-basic-dislike" title="Dislike">
			<span onClick={dislike}>
				{React.createElement(
					action === "disliked" ? DislikeFilled : DislikeOutlined
				)}
				<span className="comment-action">{dislikes}</span>
			</span>
		</Tooltip>,
		<span key="comment-basic-reply-to">Reply to</span>,
	]

	return (
		<>
			<Comment
				actions={actions}
				author={<a href="/">Han Solo</a>}
				avatar={
					<Avatar
						src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png"
						alt="Han Solo"
					/>
				}
				content={
					<p>
						We supply a series of design principles, practical
						patterns and high quality design resources (Sketch and
						Axure), to help people create their product prototypes
						beautifully and efficiently.
					</p>
				}
				datetime={
					<Tooltip title={moment().format("YYYY-MM-DD HH:mm:ss")}>
						<span>{moment().fromNow()}</span>
					</Tooltip>
				}
			/>
			<Comment
				actions={actions}
				author={<a href="/">无边的风</a>}
				avatar={<Avatar src="" alt="无边的风" />}
				content={<p>这里都没有人的吗？</p>}
				datetime={
					<Tooltip title={moment().format("YYYY-MM-DD HH:mm:ss")}>
						<span>{moment().fromNow()}</span>
					</Tooltip>
				}
			/>
			<Comment
				actions={actions}
				author={<a href="/">怒火</a>}
				avatar={
					<Avatar
						style={{
							backgroundColor: "#f56a00",
						}}
					>
						怒火
					</Avatar>
				}
				content={<p>测试一下发言，看看是什么效果</p>}
				datetime={
					<Tooltip title={moment().format("YYYY-MM-DD HH:mm:ss")}>
						<span>{moment().fromNow()}</span>
					</Tooltip>
				}
			/>
		</>
	)
}

export default RoomSite
