import React, { Component } from "react"

import { Menu } from "antd"

import "./NewsNavigation.less"

export default class NewsNavigation extends Component {
	constructor(props) {
		super(props)

		this.state = {
			tabs: [
				{ id: 1, name: "新闻" },
				{ id: 2, name: "魔兽" },
				{ id: 3, name: "LOL" },
				{ id: 4, name: "DOTA2" },
				{ id: 5, name: "守望先锋" },
				{ id: 6, name: "PUBG" },
				{ id: 7, name: "王者荣耀" },
				{ id: 8, name: "和平精英" },
				{ id: 9, name: "Finnal" },
				{ id: 10, name: "Diablo" },
			],
			current: "mail",
		}
	}

	handleClick = (e) => {
		console.log("click ", e)
		this.setState({ current: e.key })
	}

	render() {
		const { current } = this.state
		return (
			<>
				<Menu
					className="newsNav"
					// grid={{
					// 	gutter: 16,
					// 	xs: 24,
					// }}

					onClick={this.handleClick}
					selectedKeys={[current]}
					mode="horizontal"
				>
					{this.state.tabs.map((tab) => {
						return (
							<Menu.Item className="newsNavItem" key={tab.id}>
								{tab.name}
							</Menu.Item>
						)
					})}
				</Menu>
			</>
		)
	}
}
