import React, { Component } from "react"
import axios from "axios"
import "./NewsBrief.less"
import { Image, Row, Col, Card, Typography } from "antd"

const { Title } = Typography

export default class NewsBrief extends Component {
	constructor(props) {
		super(props)

		this.state = {
			news: [],
		}
	}

	//组件加载时发送请求获取新闻API数据
	componentDidMount() {
		axios
			.get("./TestData/newsdata.json")
			.then((res) => {
				console.log(res)
				this.setState({
					news: res.data.news,
				})
			})
			.catch((error) => {
				console.log(error)
			})
	}

	render() {
		return (
			<div>
				<Row>
					<Col>
						{this.state.news.map((newsObj) => {
							return (
								<Card
									className="newsCardMargin"
									key={newsObj.id}
									bordered
								>
									<div className="newsCard">
										<div>
											<Title level={5}>
												{newsObj.title}
											</Title>
											<span>{newsObj.author}</span>
											&nbsp;&nbsp;
											<span>{newsObj.timeIssued}</span>
										</div>

										<Image
											className="newsImage"
											src={newsObj.newsPic}
										/>
									</div>
								</Card>
							)
						})}
					</Col>
				</Row>
			</div>
		)
	}
}
